.section {
  width: 100%;

  &__wrapper {
    max-width: 1520px;
    margin: 0 auto;
  }

  &_theme {
    &_dark {
      background-color: black;
      color: white;
    }
  }
}

@media screen and (max-width: $breakpoint_xxl) {
  
  .section {
    &__wrapper {
      padding: 0 50px;
    }
  }

}